import { useCallback, useMemo } from 'react';
import { useSelector } from 'react-redux';
import { selectConfigControlbar } from '../selectors';
import { I_ControlbarKey } from '../types';

export const useControlbarConfig = () => {
  const controlbarConfig = useSelector(selectConfigControlbar);

  const onCheckIsShowContentControlbarItem = useCallback(
    (type: I_ControlbarKey) => {
      if (type) {
        const config = controlbarConfig[type];
        return !config.isHide;
      }
      return true;
    },
    [controlbarConfig],
  );

  const onRenderContentControlbarItem = useCallback(
    (type: I_ControlbarKey) => {
      if (type) {
        const config = controlbarConfig[type];
        if (config.type === 'icon' && config.icon)
          return (
            <i aria-hidden className={`controlbar-item__icon ${config.icon}`} />
          );
        if (config.type === 'image' && config.image?.src)
          return (
            <img
              className="controlbar-item__img"
              src={config.image?.src}
              alt="img"
            />
          );
      }
      return '';
    },
    [controlbarConfig],
  );

  return useMemo(
    () => ({
      onRenderContentControlbarItem,
      onCheckIsShowContentControlbarItem,
    }),
    [onRenderContentControlbarItem, onCheckIsShowContentControlbarItem],
  );
};
