import { Type_RefFuncKrpano } from '@components/Krpano/types';
import { useControlbarConfig } from '@containers/Home/hook/useControlbarConfig';
import { useKrpano } from '@containers/Home/hook/useKrpano';
import { LANGUAGES } from '@containers/Home/language';
import { selectLanguageProject } from '@containers/Home/selectors';
import { Tooltip } from 'antd';
import React, { memo, useState } from 'react';
import { useSelector } from 'react-redux';

interface Props {
  krpanoRef: Type_RefFuncKrpano;
}

const ControlbarTop = memo(({ krpanoRef }: Props) => {
  const languageProject = useSelector(selectLanguageProject);

  const [typeView, setTypeView] = useState<'normal' | 'mirror' | 'planet'>(
    'normal',
  );

  const { onViewNormal, onViewMirrorBall, onViewPlannet } = useKrpano({
    krpanoRef,
  });
  const { onCheckIsShowContentControlbarItem, onRenderContentControlbarItem } =
    useControlbarConfig();

  return (
    onCheckIsShowContentControlbarItem('view_mode') &&
    (onCheckIsShowContentControlbarItem('view_mode_little_planet') ||
      onCheckIsShowContentControlbarItem('view_mode_mirror') ||
      onCheckIsShowContentControlbarItem('view_mode_normal')) && (
      <div className="actGroup controlbar-top">
        {onCheckIsShowContentControlbarItem('view_mode_normal') && (
          <Tooltip
            title={LANGUAGES[languageProject].controlbarViewNormal}
            placement="top"
          >
            <button
              type="button"
              className={`act ${typeView === 'normal' ? 'active' : ''}`}
              onClick={() => {
                setTypeView('normal');
                onViewNormal();
              }}
            >
              {onRenderContentControlbarItem('view_mode_normal')}
            </button>
          </Tooltip>
        )}
        {onCheckIsShowContentControlbarItem('view_mode_mirror') && (
          <Tooltip
            title={LANGUAGES[languageProject].controlbarViewMirrorBall}
            placement="top"
          >
            <button
              type="button"
              className={`act ${typeView === 'mirror' ? 'active' : ''}`}
              onClick={() => {
                setTypeView('mirror');
                onViewMirrorBall();
              }}
            >
              {onRenderContentControlbarItem('view_mode_mirror')}
            </button>
          </Tooltip>
        )}
        {onCheckIsShowContentControlbarItem('view_mode_little_planet') && (
          <Tooltip
            title={LANGUAGES[languageProject].controlbarViewPlanet}
            placement="top"
          >
            <button
              type="button"
              className={`act ${typeView === 'planet' ? 'active' : ''}`}
              onClick={() => {
                setTypeView('planet');
                onViewPlannet();
              }}
            >
              {onRenderContentControlbarItem('view_mode_little_planet')}
            </button>
          </Tooltip>
        )}
      </div>
    )
  );
});

export default ControlbarTop;
